<template>
  <div>
    <div class="url-list">
      <div v-if="urls.length === 0">No URLs found in the Video file.</div>
      <div v-else>
        <div class="image-scroll-container">
          <div class="image-container">
            <!-- Iterate over the URLs and display each video -->
            <div v-for="(url, index) in urlsWithHttp" :key="index" class="video-container">
      
              <MovPlayer :videoUrl="url" class="video-item" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MovPlayer from './VideoPlayer.vue';

export default {
  name: 'arrayVideo',
  components: {
    MovPlayer
  },
  props: {
    projectPath: {
      type: String,
      required: true
    },
    urls: {
      type: Array,
      required: true
    }
  },
  computed: {
    urlsWithHttp() {
      // Add "https://res.cloudinary.com/alvin-tan/video/upload/" prefix to each URL
      return this.urls.map(url => url);
    }
  }
};
</script>

<style>
.url-list {
  margin-top: 20px;
}

.image-scroll-container {
  overflow-x: auto;
  white-space: nowrap;
}

.image-container {
  display: inline-block;
}

.video-container {
  margin-bottom: 20px;
}

.video-item {
  width: auto;
  max-width: 500px;
  height: auto;
}
</style>
