<template>
    <div>
        <h2>Send WhatsApp Message</h2>
        <div>
            <label for="phoneNumber">Telephone Number:</label>
            <input type="text" id="phoneNumber" v-model="phoneNumber">
        </div>
        <div>
            <label for="whatsappMessage">WhatsApp Message:</label>
            <textarea id="whatsappMessage" v-model="whatsappMessage"></textarea>
        </div>
        <button @click="sendMessage">Send WhatsApp Message</button>
    </div>
    <div>
        <div>
            <h3>{{ currentContact.name }}</h3>
            <p>{{ currentContact.phoneNumber }}</p>
            <p>{{ currentContact.message }}</p>
            <button @click="sendWhatsAppMessage">Send via WhatsApp</button>
            <button @click="startProcessing">Start</button>
            <button @click="reset">Reset</button>
            <input type="file" @change="loadCSVFile" accept=".csv">
            <button @click="exportToCSV">Export to CSV</button>
            <div>
                <label for="jumpIndex">Enter Index:</label>
                <input type="number" id="jumpIndex" v-model="jumpIndex">
                <button @click="jumpToIndex">Jump</button>
            </div>
            <p v-if="error" class="error">{{ error }}</p>
        </div>

        <h2>Contacts List</h2>
        <table>
            <thead>
                <tr>
                    <th>No</th>
                    <th>Name</th>
                    <th>Language</th>
                    <th>Phone Number</th>
                    <th>Message</th>
                    <th>Status</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(contact, index) in contacts" :key="index" :class="{ 'selected': currentIndex === index }">

                    <td>{{ index + 1 }}</td> <!-- Display index number -->
                    <td>{{ contact.name }}</td>
                    <td>{{ contact.language }}</td>
                    <td>{{ contact.phoneNumber }}</td>
                    <td>{{ contact.message }}</td>
                    <td>{{ contact.status }}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
