Kami berharap dapat berjumpa dengan anda tidak lama lagi!
` },

            ],
            contacts: [
                { name: 'John', language: "e", phoneNumber: '+601116331800', message: '@si', status: 'w' },
                { name: 'Jane', language: 'ec', phoneNumber: '+601116331801', message: '@si', status: 'P' },
                { name: 'Alex', language: 'eb', phoneNumber: '+601116331802', message: '@si', status: 'w' }
                // Add more contacts as needed
            ],
            currentIndex: 0,
            error: null
        }
    },
    computed: {
        currentContact() {
            return this.contacts[this.currentIndex] || {};
        }
    },
    methods: {
        sendMessage() {
            if (this.phoneNumber && this.whatsappMessage) {
                let msg = this.getMessage(this.whatsappMessage, "e");
                navigator.clipboard.writeText(msg);
                const cleanedPhoneNumber = this.cleanPhoneNumber(this.phoneNumber);
                const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;
                window.open(url, '_blank');
            } else {
                alert('Please enter both telephone number and WhatsApp message.');
            }
        },
        jumpToIndex() {
            if (this.jumpIndex >= 0 && this.jumpIndex < this.contacts.length) {
                this.currentIndex = this.jumpIndex;
            } else {
                // Handle invalid index
                console.error("Invalid index.");
            }
        },
        sendWhatsAppMessage() {
            const { phoneNumber, message, language, na1 } = this.currentContact;
            if (na1 !== 'w') {
                this.moveToNextContact();
                return;
            }
            if (this.validateInput(na1)) {
                let msg = this.getMessage(message, language);
                navigator.clipboard.writeText(msg);
                const cleanedPhoneNumber = this.cleanPhoneNumber(phoneNumber);
                const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;

                // Create a temporary textarea element
                const textarea = document.createElement('textarea');
                textarea.value = msg;
                document.body.appendChild(textarea);

                // Select the text within the textarea
                textarea.select();
                textarea.setSelectionRange(0, textarea.value.length);

                // Execute the copy command
                document.execCommand('copy');

                // Remove the textarea element
                document.body.removeChild(textarea);

                // Open WhatsApp link
                window.open(url, '_blank');

                this.moveToNextContact();
            }
        },
        getMessage(message, language) {
            let msg = "";
            if (message.trim() === '!si') {
                if (language.includes("e")) {
                    msg += this.messageEn.find(msg => msg.code === "si").msg + "\n\n";
                }
                if (language.includes("c")) {
                    msg += this.messageCn.find(msg => msg.code === "si").msg + "\n\n";
                }
                if (language.includes("b")) {
                    msg += this.messageBs.find(msg => msg.code === "si").msg;
                }
            }
            else if (message.trim() === '!lc') {
                if (language.includes("e")) {
                    msg += this.messageEn.find(msg => msg.code === "lc").msg + "\n\n";
                }
                if (language.includes("c")) {
                    msg += this.messageCn.find(msg => msg.code === "lc").msg + "\n\n";
                }
                if (language.includes("b")) {
                    msg += this.messageBs.find(msg => msg.code === "lc").msg;
                }
            } else if (message.trim() === '!lc2') {
                if (language.includes("e")) {
                    msg += this.messageEn.find(msg => msg.code === "lc2").msg + "\n\n";
                }
                if (language.includes("c")) {
                    msg += this.messageCn.find(msg => msg.code === "lc2").msg + "\n\n";
                }
                if (language.includes("b")) {
                    msg += this.messageBs.find(msg => msg.code === "lc2").msg;
                }
            } else if (message.trim() === '!lc3') {
                if (language.includes("e")) {
                    msg += this.messageEn.find(msg => msg.code === "lc3").msg + "\n\n";
                }
                if (language.includes("c")) {
                    msg += this.messageCn.find(msg => msg.code === "lc3").msg + "\n\n";
                }
                if (language.includes("b")) {
                    msg += this.messageBs.find(msg => msg.code === "lc3").msg;
                }
            }
            else
                msg = message;
            return msg;
        },
        validateInput(message) {
            if (!message) {
                this.error = 'Please enter a message.';
                return false;
            }
            this.error = null;
            return true;
        },
        moveToNextContact() {
            let checkedContacts = 0;
            do {
                this.currentIndex++;
                if (this.currentIndex >= this.contacts.length) {
                    this.currentIndex = 0; // Reset index to loop through contacts
                    checkedContacts++;
                    if (checkedContacts >= this.contacts.length) {
                        // If all contacts have been checked and none have 'w' status, break out of the loop
                        break;
                    }
                }
            } while (this.contacts[this.currentIndex].na1 !== 'w');
        },
        startProcessing() {
            this.currentIndex = 0;
            let checkedContacts = 0;
            while (this.contacts[this.currentIndex].na1 !== 'w') {
                this.moveToNextContact();
                checkedContacts++;
                if (checkedContacts >= this.contacts.length) {
                    // If all contacts have been checked and none have 'w' status, break out of the loop
                    break;
                }
            }
        },
        reset() {
            this.currentIndex = 0;
        },
        loadCSVFile(event) {
            const file = event.target.files[0];
            if (!file) {
                this.error = 'Please select a file.';
                return;
            }
            const reader = new FileReader();
            reader.onload = (e) => {
                const contents = e.target.result;
                this.parseCSV(contents);
            };
            reader.readAsText(file);
        },
        parseCSV(contents) {
            const lines = contents.split('\n');
            const result = [];
            for (const line of lines) {
                if (line.trim() === '') continue;
                const [name, language, phoneNumber, message, status, date, na1] = line.split(',');
                if (name && language && phoneNumber && message && status) {
                    result.push({ name: name.trim(), language: language.trim(), phoneNumber: phoneNumber.trim(), message: message.trim(), status: status.trim(), date: date.trim(), na1: na1.trim() });
                }
            }
            // Assign each property of the new array to the existing array
            this.contacts.splice(0, this.contacts.length, ...result);
        },
        exportToCSV() {
            const csvContent = this.contacts.map(contact => {
                return `${contact.name},${contact.language},${contact.phoneNumber},${contact.message},${contact.status}`;
            }).join('\n');

            const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
            const link = document.createElement('a');
            const url = URL.createObjectURL(blob);
            link.setAttribute('href', url);
            link.setAttribute('download', 'contacts.csv');
            link.style.visibility = 'hidden';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }
}
</script>

