<template>
  <div>
    <!-- <p>Video URL: {{ videoUrl }}</p> -->
    <div v-html="videoHtml" class="video-container"></div>
  </div>
</template>

<script>
export default {
  props: {
    videoUrl: {
      type: String,
      required: true
    }
  },
  computed: {
    videoHtml() {
      return `<video controls autoplay>
                <source src="${this.videoUrl}" type="video/mp4">
                Your browser does not support the video tag.
              </video>`;
    }
  }
}
</script>

<style>
.video-container {
  max-width: 400px;
  margin: auto;
}
video {
  width: 100%;
  height: auto;
}
</style>
