<template>
  <div>
    <img :src="thumbnailUrl" alt="Video Thumbnail" @click="openVideo">
  </div>
</template>

<script>
export default {
  props: {
    videoId: {
      type: String,
      required: true
    }
  },
  computed: {
    thumbnailUrl() {
      return `https://img.youtube.com/vi/${this.videoId}/hqdefault.jpg`;
    }
  },
  methods: {
    openVideo() {
      window.open(`https://www.youtube.com/watch?v=${this.videoId}`, '_blank');
    }
  }
}
</script>

